@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Offside&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



/*===== GOOGLE FONTS =====*/
/*===== VARIABLES CSS =====*/
:root {
  --header-height: 3rem;
  --font-semi: 600;
}

/*===== Colores =====*/
:root {
  --first-color: #4070F4;
  --second-color: #0E2431;
}

/*===== Fuente y tipografia =====*/
:root {
  --body-font: 'Poppins', sans-serif;
  --big-font-size: 2rem;
  --h2-font-size: 1.25rem;
  --normal-font-size: 0.938rem;
}

@media screen and (min-width: 768px) {
  :root {
    --big-font-size: 3.5rem;
    --h2-font-size: 2rem;
    --normal-font-size: 1rem;
  }
}

/*===== Margenes =====*/
:root {
  --mb-1: 0.5rem;
  --mb-2: 1rem;
  --mb-3: 1.5rem;
  --mb-4: 2rem;
  --mb-5: 2.5rem;
  --mb-6: 3rem;
}

/*===== z index =====*/
:root {
  --z-back: -10;
  --z-normal: 1;
  --z-tooltip: 10;
  --z-fixed: 100;
}

/*===== BASE =====*/
*, ::before, ::after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 3rem 0 0 0;
  margin: var(--header-height) 0 0 0;
  font-family: 'Poppins', sans-serif;
  font-family: var(--body-font);
  font-size: 0.938rem;
  font-size: var(--normal-font-size);
  color: #0E2431;
  color: var(--second-color);
}

h1, h2, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

img { 
  box-shadow: -4px 32px 49px -1px rgba(99,107,96,0.69);
  border-radius: 50px;
  margin-left: 240px;
  max-width: 50%;
  height: auto;
  display: inline;
}

img:hover{
  transform: scale(1.1);
}

/*===== CLASS CSS ===== */
.section-title {
  position: relative;
  font-size: 1.25rem;
  font-size: var(--h2-font-size);
  color: #4070F4;
  color: var(--first-color);
  margin-top: 1rem;
  margin-top: var(--mb-2);
  margin-bottom: 2rem;
  margin-bottom: var(--mb-4);
  text-align: center;
}

.section-title::after {
  position: absolute;
  content: '';
  width: 64px;
  height: 0.18rem;
  left: 0;
  right: 0;
  margin: auto;
  top: 2rem;
  background-color: #4070F4;
  background-color: var(--first-color);
}

.section {
  padding-top: 3rem;
  padding-bottom: 2rem;
}

/*===== LAYOUT =====*/
.bd-grid {
  max-width: 1024px;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 100%;
      grid-template-columns: 100%;
  grid-column-gap: 2rem;
  width: calc(100% - 2rem);
  margin-left: 1rem;
  margin-left: var(--mb-2);
  margin-right: 1rem;
  margin-right: var(--mb-2);
}

.l-header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  z-index: var(--z-fixed);
  background-color: #fff;
  box-shadow: 0 1px 4px rgba(146, 161, 176, 0.15);
}

/*===== NAV =====*/
.nav {
  height: 3rem;
  height: var(--header-height);
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 600;
  font-weight: var(--font-semi);
}

@media screen and (max-width: 768px) {
  .nav__menu {
    position: fixed;
    top: 3rem;
    top: var(--header-height);
    right: -100%;
    width: 80%;
    height: 100%;
    padding: 2rem;
    background-color: #0E2431;
    background-color: var(--second-color);
    transition: .5s;
  }
}
a{
  text-decoration: none;
}

.nav__item {
  margin-bottom: 2rem;
  margin-bottom: var(--mb-4);
}

.nav__link {
  position: relative;
  color: #fff;
  text-decoration: none;
  list-style: none;
}

.nav__link:hover {
  position: relative;
  list-style: none;
}

.nav__link:hover::after {
  position: absolute;
  content: '';
  width: 100%;
  height: 0.18rem;
  left: 0;
  top: 2rem;
  background-color: #4070F4;
  background-color: var(--first-color);
  list-style: none;
}

.nav__logo {
  color: #0E2431;
  color: var(--second-color);
}

.nav__toggle {
  color: #0E2431;
  color: var(--second-color);
  font-size: 1.5rem;
  cursor: pointer;
  list-style: none;
}

/*Active menu*/
.active::after {
  position: absolute;
  content: '';
  width: 100%;
  height: 0.18rem;
  left: 0;
  top: 2rem;
  background-color: #4070F4;
  background-color: var(--first-color);
}

/*=== Show menu ===*/
.show {
  right: 0;
}

/*===== HOME =====*/
.home {
  height: calc(100vh - 3rem);
  grid-row-gap: 1rem;
  row-gap: 1rem;
}

.home__data {
  -ms-grid-row-align: center;
  align-self: center;
}

.home__title {
  font-size: 2rem;
  font-size: var(--big-font-size);
  margin-bottom: 2.5rem;
  margin-bottom: var(--mb-5);
}

.home__title-color {
  color: #4070F4;
  color: var(--first-color);
}

.home__social {
  display: flex;
  flex-direction: column;
}

.home__social-icon {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  margin-bottom: 1rem;
  margin-bottom: var(--mb-2);
  font-size: 1.5rem;
  color: #0E2431;
  color: var(--second-color);
}

.home__social-icon:hover {
  color: #4070F4;
  color: var(--first-color);
}

.home__img {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 295px;
}

/*BUTTONS*/
.button {
  display: inline-block;
  background-color: #4070F4;
  background-color: var(--first-color);
  color: #fff;
  padding: .75rem 2.5rem;
  font-weight: 600;
  font-weight: var(--font-semi);
  border-radius: .5rem;
}


.button:hover {
  box-shadow: 0px 10px 36px rgba(0, 0, 0, 0.15);
}




/* ===== ABOUT =====*/
.about__container {
  grid-row-gap: 2rem;
  row-gap: 2rem;
  text-align: center;
}

.about__subtitle {
  margin-bottom: 1rem;
  margin-bottom: var(--mb-2);
}
.about__text{
  text-indent: 50px;
}

.projects-squares{
 
 
  justify-content:center;
  align-items:center;
  align-content: center;
  margin-right: 100px;



}
.projects-squares:hover{
  transform: scale(1.9);
}

.projects-h1{
  position: relative;
  font-size: 1.25rem;
  font-size: var(--h2-font-size);
  color: #4070F4;
  color: var(--first-color);
  margin-top: 1rem;
  margin-top: var(--mb-2);
  margin-bottom: 2rem;
  margin-bottom: var(--mb-4);
  text-align: center;
}


.projects-container{
 
  margin-right: 200px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin: 20px;
  
 
}
















.github-img-img{
  width: 600px;
}
.github-img-img:hover{
  transform: scale(2);
}

.about__img {
  -ms-grid-column-align: center;
      justify-self: center;
      

}

.about__img .resume-resume:hover{
  transform: scale(2);
}


.about__img img {
  width: 170px;

  
}


/* ===== SKILLS =====*/
.skills__container {
  grid-row-gap: 2rem;
  row-gap: 2rem;
  text-align: center;
}

.skills__subtitle {
  margin-bottom: 1rem;
  margin-bottom: var(--mb-2);
}

.skills__text {
  margin-bottom: 2rem;
  margin-bottom: var(--mb-4);
}

.skills__data {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  font-weight: 600;
  font-weight: var(--font-semi);
  padding: .5rem 1rem;
  margin-bottom: 2rem;
  margin-bottom: var(--mb-4);
  border-radius: .5rem;
  box-shadow: 0px 4px 25px rgba(14, 36, 49, 0.15);
}

.skills__icon {
  font-size: 2rem;
  margin-right: 1rem;
  margin-right: var(--mb-2);
  color: #4070F4;
  color: var(--first-color);
}

.skills__names {
  display: flex;
  align-items: center;
}

.skills__bar {
  position: absolute;
  left: 0;
  bottom: 0;
  background-color: #4070F4;
  background-color: var(--first-color);
  height: .25rem;
  border-radius: .5rem;
  z-index: -10;
  z-index: var(--z-back);
}

.skills__html {
  width: 95%;
}

.skills__css {
  width: 85%;
}

.skills__js {
  width: 65%;
}

.skills__ux {
  width: 85%;
}

.skills__img {
  border-radius: .5rem;
}

/* ===== WORK =====*/
.work__container {
  grid-row-gap: 2rem;
  row-gap: 2rem;
}

.work__img {
  box-shadow: 0px 4px 25px rgba(14, 36, 49, 0.15);
  border-radius: .5rem;
  overflow: hidden;
}

.work__img img {
  transition: 1s;
  cursor: pointer;
}

.work__img img:hover {
  transform: scale(1.1);
}

/* ===== CONTACT =====*/
.contact__input {
  width: 100%;
  font-size: 0.938rem;
  font-size: var(--normal-font-size);
  font-weight: 600;
  font-weight: var(--font-semi);
  padding: 1rem;
  border-radius: .5rem;
  border: 1.5px solid #0E2431;
  border: 1.5px solid var(--second-color);
  outline: none;
  margin-bottom: 2rem;
  margin-bottom: var(--mb-4);
}

.contact__button {
  display: block;
  border: none;
  outline: none;
  font-size: 0.938rem;
  font-size: var(--normal-font-size);
  cursor: pointer;
  margin-left: auto;
}

/* ===== FOOTER =====*/
.footer {
  background-color: #0E2431;
  background-color: var(--second-color);
  color: #fff;
  text-align: center;
  font-weight: 600;
  font-weight: var(--font-semi);
  padding: 2rem 0;
}

.footer__title {
  font-size: 2rem;
  margin-bottom: 2rem;
  margin-bottom: var(--mb-4);
}

.footer__social {
  margin-bottom: 2rem;
  margin-bottom: var(--mb-4);
}

.footer__icon {
  font-size: 1.5rem;
  color: #fff;
  margin: 0 1rem;
  margin: 0 var(--mb-2);
}

/* ===== MEDIA QUERIES=====*/
@media screen and (min-width: 768px) {
  body {
    margin: 0;
  }
  .section {
    padding-top: 4rem;
    padding-bottom: 3rem;
  }
  .section-title {
    margin-bottom: 3rem;
    margin-bottom: var(--mb-6);
  }
  .section-title::after {
    width: 80px;
    top: 3rem;
  }
  .nav {
    height: calc(3rem + 1rem);
    height: calc(var(--header-height) + 1rem);
  }
  .nav__list {
    display: flex;
    padding-top: 0;
    text-decoration: none;
  }
  .nav__item {
    margin-left: 3rem;
    margin-left: var(--mb-6);
    margin-bottom: 0;
  }
  .nav__toggle {
    display: none;
  }
  .nav__link {
    color: #0E2431;
    color: var(--second-color);
  }
  .home {
    height: 100vh;
  }
  .home__data {
    align-self: flex-end;
  }
  .home__social {
    padding-top: 0;
    padding-bottom: 2.5rem;
    flex-direction: row;
    align-self: flex-end;
  }
  .home__social-icon {
    margin-bottom: 0;
    margin-right: 2rem;
    margin-right: var(--mb-4);
  }
  .home__img {
    width: 457px;
    bottom: 15%;
  }
  .about__container {
    -ms-grid-columns: (1fr);
        grid-template-columns: repeat(2, 1fr);
    align-items: center;
    text-align: left;
    text-align: initial;
  }
  .about__img img {
    width: 300px;
  }
  .skills__container {
    -ms-grid-columns: (1fr);
        grid-template-columns: repeat(2, 1fr);
    align-items: center;
    text-align: left;
    text-align: initial;
  }
  .work__container {
    -ms-grid-columns: (1fr);
        grid-template-columns: repeat(3, 1fr);
    -ms-grid-rows: (1fr);
        grid-template-rows: repeat(2, 1fr);
    -webkit-column-gap: 2rem;
            grid-column-gap: 2rem;
            column-gap: 2rem;
  }
  .contact__form {
    width: 360px;
  }
  .contact__container {
    justify-items: center;
  }
}
.skills__text{
  text-align: left;
  text-indent: 20px;
}


.car-item{
  width: auto;
  height: auto;
}
.video-code{
  margin: 20vh;
  margin-bottom: 20px;
  text-decoration: none;

}
@media screen and (min-width: 1024px) {
  .bd-grid {
    margin-left: auto;
    margin-right: auto;
  }
  .home__img {
    right: 10%;
  }
}



